.footer__copyright {
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  color: #545454;
  margin: 0;
}

@media screen and (max-width:1023px) {
  .footer__copyright {
    font-size: 16px;
  }
}

@media screen and (max-width:424px) {
  .footer__copyright {
    font-size: 14px;
  }
}

